// Dependencies
import React, { useState, useCallback } from "react";

// Styles
import "./tailwind.output.css";

const BUTTON_GREEN =
  "bg-green-400 rounded-lg px-2 py-1 font-semibold text-white block mt-2";

const BASE_URL = "http://episodes-fm.herokuapp.com/v1";

const APICommand = ({ title, details, children }) => (
  <div className="mb-6">
    <h3 className="text-lg text-green-400">{title}</h3>
    <p className="text-sm mb-3">{details}</p>
    {children}
  </div>
);

const GetEpisodeWithShareURLCommand = () => {
  const [shareURL, setShareURL] = useState("");
  const doAPICall = useCallback(() => {
    window.location.href = `${BASE_URL}/episode/lookup/url/${shareURL}`;
  }, [shareURL]);

  return (
    <APICommand
      title="Get Episode with Share URL"
      details="Provides episode, podcast details & third-party URLs"
    >
      <input
        className="bg-gray-50 border-2 rounded-lg px-2 py-1 mr-2"
        type="text"
        value={shareURL}
        onChange={(e) => setShareURL(e.target.value)}
        placeholder="Share URL"
      />
      <button className={BUTTON_GREEN} onClick={() => doAPICall()}>
        Get Episode
      </button>
    </APICommand>
  );
};

const GetEpisodeWithFeedAndGUID = () => {
  const [feedURL, setFeedURL] = useState("");
  const [guid, setGUID] = useState("");

  const doAPICall = useCallback(() => {
    window.location.href = `${BASE_URL}/episode/lookup/feed/${feedURL}/${guid}`;
  }, [feedURL, guid]);

  return (
    <APICommand
      title="Get Episode with Feed URL & GUID"
      details="Provides episode, podcast details & third-party URLs"
    >
      <input
        className="bg-gray-50 border-2 rounded-lg px-2 py-1 mr-2"
        type="text"
        value={feedURL}
        onChange={(e) => setFeedURL(e.target.value)}
        placeholder="Feed URL"
      />

      <input
        className="bg-gray-50 border-2 rounded-lg px-2 py-1 mr-2"
        type="text"
        value={guid}
        onChange={(e) => setGUID(e.target.value)}
        placeholder="GUID"
      />

      <button className={BUTTON_GREEN} onClick={() => doAPICall()}>
        Get Episode
      </button>
    </APICommand>
  );
};

const GetAllPlatforms = () => {
  const doAPICall = () => (window.location.href = `${BASE_URL}/platforms`);
  return (
    <APICommand
      title="Get all platforms"
      details="Returns all available third-party platforms"
    >
      <button className={BUTTON_GREEN} onClick={() => doAPICall()}>
        Get Platforms
      </button>
    </APICommand>
  );
};

const GetPodcastWithFeedURLCommand = () => {
  const [feedURL, setFeedURL] = useState("");
  const doAPICall = useCallback(() => {
    window.location.href = `${BASE_URL}/podcast/lookup/feed/${feedURL}`;
  }, [feedURL]);

  return (
    <APICommand
      title="Get Podcast with Feed URL"
      details="Provides third-party podcast URLs"
    >
      <input
        className="bg-gray-50 border-2 rounded-lg px-2 py-1 mr-2"
        type="text"
        value={feedURL}
        onChange={(e) => setFeedURL(e.target.value)}
        placeholder="Feed URL"
      />
      <button className={BUTTON_GREEN} onClick={() => doAPICall()}>
        Get Feeds
      </button>
    </APICommand>
  );
};

const GetAllUsers = () => {
  const doAPICall = () => (window.location.href = `${BASE_URL}/users`);
  return (
    <APICommand title="Get all users" details="Returns all users">
      <button className={BUTTON_GREEN} onClick={() => doAPICall()}>
        Get Users
      </button>
    </APICommand>
  );
};

const CreateUser = () => {
  const [email, setEmail] = useState("");
  const [role, setRole] = useState("");
  const doAPICall = useCallback(() => {
    // window.location.href = `${BASE_URL}/episode/lookup/url/${shareURL}`;
  }, [email, role]);

  return (
    <APICommand title="Create User" details="Creates API User">
      <input
        className="bg-gray-50 border-2 rounded-lg px-2 py-1 mr-2"
        type="text"
        value={email}
        onChange={(e) => setEmail(e.target.value)}
        placeholder="Email"
      />

      <select onChange={(e) => setRole()}>
        <option>Role</option>
        <option value="admin">Admin</option>
        <option value="consumer">Consumer</option>
      </select>

      <button className={BUTTON_GREEN} onClick={() => doAPICall()}>
        Create User
      </button>
    </APICommand>
  );
};

const DeleteUser = () => {
  const [id, setId] = useState("");
  const doAPICall = useCallback(() => {
    // window.location.href = `${BASE_URL}/episode/lookup/url/${shareURL}`;
  }, [id]);

  return (
    <APICommand title="Delete User" details="Creates API User">
      <input
        className="bg-gray-50 border-2 rounded-lg px-2 py-1 mr-2"
        type="text"
        value={id}
        onChange={(e) => setId(e.target.value)}
        placeholder="User ID"
      />

      <button className={BUTTON_GREEN} onClick={() => doAPICall()}>
        Delete User
      </button>
    </APICommand>
  );
};

const H2 = ({ children, name }) => (
  <h2 name={name} className="text-xl font-bold mt-5">
    {children}
  </h2>
);

const A_CLASS = "text-green-400 mr-5 underline";

const App = () => {
  return (
    <div className="min-h-screen p-8">
      <h1 className="text-3xl font-bold text-green-400">
        Episode.fm API documentation
      </h1>

      <div>
        <a className={A_CLASS} href="#episodes">
          Episodes
        </a>
        <a className={A_CLASS} href="#platforms">
          Platforms
        </a>
        <a className={A_CLASS} href="#podcasts">
          Podcasts
        </a>
        <a className={A_CLASS} href="#users">
          Users
        </a>
      </div>

      <H2 name="episode">Episodes</H2>
      <GetEpisodeWithShareURLCommand />
      <GetEpisodeWithFeedAndGUID />
      <hr className="my-10" />

      <H2 name="platforms">Platforms</H2>
      <GetAllPlatforms />
      <hr className="my-10" />

      <H2 name="podcasts">Podcasts</H2>
      <GetPodcastWithFeedURLCommand />
      <hr className="my-10" />

      <H2 name="users">Users</H2>
      <GetAllUsers />
      <CreateUser />
      <DeleteUser />
    </div>
  );
};

export default App;
